html,body{
	margin: 0;
	/*width: 100%;*/
	/*height: 100%;*/
	/*overflow: hidden;*/
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	font-family: 'Sans Pro', 'sans-serif';
	/*font-family: 'Source-Pro', 'sans-serif';*/
	text-rendering: optimizeSpeed;
}

* {
	outline: none;
	-webkit-box-sizing: border-box;
	    box-sizing: border-box;
}

ul{
	list-style-type: none;
	margin: 0;
	padding: 0;
}

h1,h2,h3,h4,h5{
	margin: 0;
	font-weight: 600;
}

p {
	margin: 0;
}

a{
	text-decoration: none;
}


